import React, { useState } from "react";
import { Layout, Menu } from "antd";
import {
	ExperimentOutlined,
	DesktopOutlined,
	GithubOutlined,
	FileOutlined,
	TeamOutlined,
	UserOutlined,
} from "@ant-design/icons";
import "antd/dist/antd.css";

const { Sider } = Layout;
const { SubMenu } = Menu;

const SiderMenu = ({ currMenuNo, selMenuEvent, menuList }) => {
	const [collapsed, setCollapsed] = useState(false);

	const logoStyle = {
		style: {
			height: "32px",
			margin: "16px",
			color: "#fff",
		},
	};

	const onCollapse = () => {
		setCollapsed(!collapsed);
	};

	const [menuItems, setMenuItems] = useState(undefined);

	const renderSiderMenu = () => {
		let tempMenuItem = [];

		menuList.map((item, index) => {
			if (!item.subMenu) {
				tempMenuItem = tempMenuItem.concat(
					<Menu.Item
						key={item.menuCode}
						icon={<GithubOutlined />}
						onClick={() => selMenuEvent(item.menuCode)}
					>
						{item.menuName}
					</Menu.Item>
				);
			} else {
				let tempSubMenuItems = [];

				item.subMenu.map((subItem, subIndex) => {
					tempSubMenuItems = tempSubMenuItems.concat(
						<Menu.Item
							key={`subMenuItem${index}${subIndex}${subItem.menuCode}`}
							onClick={() => selMenuEvent(subItem.menuCode)}
						>
							{subItem.menuName}
						</Menu.Item>
					);
				});

				tempMenuItem = tempMenuItem.concat(
					<SubMenu
						key={`subMenu${index}`}
						icon={<UserOutlined />}
						title='User'
						children={tempSubMenuItems}
					/>
				);
			}
		});

		setMenuItems(tempMenuItem);
	};

	useState(() => {
		renderSiderMenu();
	}, menuList);

	return (
		<Sider collapsible collapsed={collapsed} onCollapse={onCollapse}>
			<div {...logoStyle}>
				<ExperimentOutlined
					style={{ color: "#fff", fontSize: "150%" }}
					size='large'
				/>{" "}
				심심해
			</div>
			<Menu theme='dark' defaultSelectedKeys={["1"]} mode='inline'>
				{menuItems}
			</Menu>
		</Sider>
	);
};

export default SiderMenu;
